export const TableHead = {// 需要传进来一个this对象
  data() {
    return {
      footerData: {},
      tableColumn: [
        {
          type: "checkbox",
          width: 44,
          align: 'center'
        }, {
          type: 'index',
          title: '序号',
          width: 46
        },
        {
          field: 'name',
          title: '操作',
          width: 60,
          fixed: "right",
          slots: {
            default: (scope, h) => {
              return [
                <el-dropdown onCommand={(val) => this.handleCommand(val, scope.row)} style="cursor:pointer">
                  <i class="el-icon-more el-icon--right"></i>
                  <el-dropdown-menu slot="dropdown">
                    <el-dropdown-item command="seeF" disabled={!(this.authority.b_info == 1)}>查看</el-dropdown-item>
                    <el-dropdown-item command="modifyF" disabled={!(scope.row.auditor_status == 1 && this.authority.b_edit == 1)}
                    >修改</el-dropdown-item>
                    <el-dropdown-item command="salesPickingOrders" v-show={scope.row.after_auditor_print_status ? (this.authority.b_printingpick && scope.row.auditor_status == 2) : this.authority.b_printingpick}
                    >拣货单打印</el-dropdown-item>
                    <el-dropdown-item command="outboundOrder" v-show={scope.row.after_auditor_print_status ? (this.authority.b_printingoutstock && scope.row.auditor_status == 2) : this.authority.b_printingoutstock}
                    >出库单打印</el-dropdown-item>
                    <el-dropdown-item command="autoPartsList" v-show={scope.row.after_auditor_print_status ? (this.authority.b_printingpartslist && scope.row.auditor_status == 2) : this.authority.b_printingpartslist}
                    >汽配单打印</el-dropdown-item>
                  </el-dropdown-menu>
                </el-dropdown>
              ]
            }
          }
        },
        {
          title: '销售组织',
          field: 'company_name',
          width: 150
        },
        {
          title: '单据号',
          field: 'instorage_sn',
          width: 170,
          slots: {
            default: (scope, h) => {
              return [
                <a style={{ cursor: 'pointer' }} onClick={() => { this.seeF(scope.row) }} type="text" size="small">
                  {scope.row.outstock_sn} </a>
              ]
            }
          }
        },
        {
          title: '客户名称',
          field: 'customer_name',
          width: 100
        },
        {
          title: '业务员',
          field: 'business_name',
          width: 84
        },
        {
          title: '订单来源',
          field: 'is_erp_title',
          width: 80
        },
        {
          title: '出库数量',
          field: 'outstock_num',
          headerAlign: "right",
          align: "right",
          width: 80
        },
        {
          title: '优惠金额',
          field: 'discount_total_amount',
          headerAlign: "right",
          align: "right",
          width: 80
        },
        {
          title: '出库金额',
          field: 'outstock_price',
          headerAlign: "right",
          align: "right",
          width: 100,
          formatter: 'priceFormat'
        },
        {
          title: '门店优惠金额',
          field: 'shop_discount_price',
          headerAlign: "right", // 表头对齐方式
          align: "right", // 列对齐方式
          width: 100
        },
        {
          title: '原价金额',
          field: 'original_price',
          headerAlign: "right", // 表头对齐方式
          align: "right", // 列对齐方式
          width: 100
        },
        {
          field: 'sub_revenue_price',
          showOverflow: true,
          headerAlign: "right",
          align: "right",
          title: '税额',
          width: 100
        },
        {
          field: 'sub_no_revenue_price',
          showOverflow: true,
          headerAlign: "right",
          align: "right",
          title: '不含税金额',
          width: 100
        },
        {
          title: '使用返利',
          field: 'rebate_price',
          headerAlign: "right",
          align: "right",
          width: 100,
          formatter: 'priceFormat'
        },
        {
          title: '使用授信',
          field: 'credit_price',
          headerAlign: "right",
          align: "right",
          width: 100,
          formatter: 'priceFormat'
        },
        {
          title: '使用预收',
          field: 'deposits_price',
          headerAlign: "right",
          align: "right",
          width: 100,
          formatter: 'priceFormat'
        },
        {
          title: '回款金额',
          field: 'receipt_price',
          headerAlign: "right",
          align: "right",
          sortable: true,
          sortMethod: (a, b) => {
            return a.receipt_price - b.receipt_price;
          },
          formatter: 'priceFormat',
          width: 100
        },
        {
          title: '未回款金额',
          field: 'unreceipt_price',
          headerAlign: "right",
          align: "right",
          sortable: true,
          formatter: 'priceFormat',
          width: 100,
          sortMethod: (a, b) => {
            return a.unreceipt_price - b.unreceipt_price;
          },
        },
        {
          title: '已退货金额',
          field: 'return_amount',
          headerAlign: "right",
          align: "right",
          width: 100,
          formatter: 'priceFormat'
        },

        {
          title: '单据状态',
          field: 'auditor_status_name',
          width: 130
        },
        {
          title: '审核人',
          field: 'auditor_name',
          width: 130
        },
        {
          title: '制单人',
          field: 'add_user_name',
          width: 130
        },
        {
          title: '业务日期',
          field: 'business_time',
          width: 130
        },
        {
          title: '单据时间',
          field: 'add_time',
          width: 130
        },
        {
          title: '审核时间',
          field: 'auditor_time',
          width: 130
        },
        {
          title: '修改人',
          field: 'update_user_name',
          width: 130
        },
        {
          title: '修改时间',
          field: 'update_time',
          width: 130
        },
        {
          title: '关联订单号',
          field: 'order_sn',
          width: 156
        },
        {
          title: '电商订单号',
          field: 'ec_order_sn',
          width: 156,
          formatter: function ({ row, column }) {
            return row.ec_order_sn != 0 ? row[column.property] : ''
          }

        },
        {
          title: '部门',
          field: 'department_name',
          width: 100
        },
        {
          title: '回款方式',
          field: 'payment_method',
          width: 100
        },
        {
          title: '物流公司',
          field: 'settlement_customer_name',
          width: 100
        },
        // {
        //   title: '物流票号',
        //   field: 'logistics_number',
        //   width: 160
        // },
        {
          title: '意向物流',
          field: 'intentional_logistics',
          width: 100
        },
        {
          title: '客户分类',
          field: 'customer_type_name',
          width: 100
        },
        {
          title: '详细地址',
          field: 'address',
          width: 300
        },
        {
          title: '打印次数',
          field: 'print_times',
          width: 80
        },
        {
          title: '备注',
          field: 'abstract',
          width: 200
        },
        {
          title: '开票信息',
          field: 'invoice_info',
          width: 200
        },
      ]
    }
  },
  methods: {
    dealData(result) {
      if (result.data.code === 200) {
        this.footerData = result.data.data.desc
      }
    },
    footerMethod({ columns, data }) {
      return [
        columns.map((column, columnIndex) => {
          if (columnIndex === 1) {
            return "页小计";
          }
          if (["outstock_num", "outstock_price", 'discount_total_amount', 'sub_revenue_price', 'sub_no_revenue_price', 'rebate_price', 'credit_price', 'deposits_price', "receipt_price", 'return_amount', "unreceipt_price", "shop_discount_price", "original_price"].includes(column.property)) {
            var num = this.$utils.sum(data, column.property);
            if (column.property === "outstock_num") {
              return num
                .toString()
                .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1" + ",");
            } else {
              return num
                .toFixed(2)
                .toString()
                .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1" + ",");
            }
          }
          return null;
        })
        ,
        columns.map((column, columnIndex) => {
          if (columnIndex === 1) {
            return "合计";
          }
          if (column.property === 'outstock_num' && this.footerData.sum_number) {
            return this.footerData.sum_number
              .toString()
              .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1" + ",");
          }
          var result = null
          if (column.property === 'outstock_price' && this.footerData.sum_outprice) {
            result = this.footerData.sum_outprice
          }
          if (column.property === 'credit_price' && this.footerData.sum_credit_price) {
            result = this.footerData.sum_credit_price
          }
          if (column.property === 'rebate_price' && this.footerData.sum_rebate_price) {
            result = this.footerData.sum_rebate_price
          }
          if (column.property === 'discount_total_amount' && this.footerData.sum_discount_total_amount) {
            result = this.footerData.sum_discount_total_amount
          }
          if (column.property === 'sub_revenue_price' && this.footerData.sub_revenue_price) {
            result = this.footerData.sub_revenue_price
          }
          if (column.property === 'sub_no_revenue_price' && this.footerData.sub_no_revenue_price) {
            result = this.footerData.sub_no_revenue_price
          }
          if (column.property === 'shop_discount_price' && this.footerData.shop_discount_price_total) {
            result = this.footerData.shop_discount_price_total
          }
          if (column.property === 'original_price' && this.footerData.original_price_total) {
            result = this.footerData.original_price_total
          }
          if (column.property === 'return_amount' && this.footerData.sum_return_amount) {
            result = this.footerData.sum_return_amount
          }
          if (column.property === 'deposits_price' && this.footerData.sum_deposits_price) {
            result = this.footerData.sum_deposits_price
          }
          if (column.property === 'receipt_price' && this.footerData.sum_return_price) {
            result = this.footerData.sum_return_price
          }
          if (column.property === 'unreceipt_price' && this.footerData.sum_unreturn_price) {
            result = this.footerData.sum_unreturn_price
          }
          if (result !== null) return (result - 0)
            .toFixed(2)
            .toString()
            .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1" + ",");
          if (["outstock_num", "outstock_price", 'credit_price', 'rebate_price', 'discount_total_amount', "receipt_price", 'return_amount', 'deposits_price', "unreceipt_price"].includes(column.property)) {
            return '加载中...'
          }
          return null;
        })]
    }
  }
}