            <erpEmployee v-model="params.business_id" :fatherParams='params' modelLabel="business_name" :searchParams="{ company_id: params.company_id }" />
          </el-form-item>
          <el-form-item label="单据状态" size="mini">
            <el-select v-model="params.auditor_status" placeholder="请选择">
              <el-option label="全部" :value="0">全部</el-option>
