            <el-input v-model="params.order_sn" placeholder="请输入" @keyup.enter.native="onSearch(1)" clearable></el-input>
          </el-form-item>
          <el-form-item label="电商订单号" size="mini">
            <el-input v-model="params.ec_order_sn" placeholder="请输入" @keyup.enter.native="onSearch(1)" clearable></el-input>
          </el-form-item>
          <el-form-item label="制单人" size="mini">
            <el-select filterable v-model="params.add_user_id" @focus="EmployeeListInvok" @change="(val)=>{employeeChange(val,'add_user_name')}">
              <el-option :label="item.employee_name" :value="item.employee_id" :key="item.employee_id" v-for="item in employeeList"></el-option>
            </el-select>
            <!-- <el-input v-model="params.add_user_name" placeholder="请输入"></el-input> -->
          </el-form-item>
          <el-form-item label="审核人" size="mini">
            <el-select filterable v-model="params.auditor_id" @focus="EmployeeListInvok" @change="(val)=>{employeeChange(val,'auditor_name')}">
              <el-option :label="item.employee_name" :value="item.employee_id" :key="item.employee_id" v-for="item in employeeList"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="仓库名称" size="mini">
            <el-select clearable v-model="params.warehouse_id" multiple collapse-tags @change="warehouseChange" filterable placeholder="请选择" @focus="warehouseInvok">
              <el-option :label="item.warehouse_name" :value="item.warehouse_id" :key="item.warehouse_id" v-for="item in warehouseList"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="审核日期" size="mini">
            <el-date-picker v-model=" auditValue" type="daterange" range-separator="-" value-format="yyyy-MM-dd" start-placeholder="开始日期" end-placeholder="结束日期">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="回款状态" size="mini">
            <el-select clearable v-model="params.return_price_status" @clear="clickClear(params,['return_price_status'])" placeholder="请选择">
              <el-option label="全部" :value="0">全部</el-option>
              <el-option label="未回款" :value="1">未回款</el-option>
              <el-option label="部分回款" :value="2">部分回款</el-option>
              <el-option label="全部回款" :value="3">全部回款</el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="发货状态" size="mini">
            <el-select clearable v-model="params.deliver_status" @clear="clickClear(params,['deliver_status'])" placeholder="请选择">
              <el-option label="全部" :value="0">全部</el-option>
              <el-option label="未发货" :value="1">未发货</el-option>
              <el-option label="部分发货" :value="2">部分发货</el-option>
              <el-option label="全部发货" :value="3">全部发货</el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="详细地址" size="mini">
            <el-input v-model="params.address" placeholder="请输入" @keyup.enter.native="onSearch(1)" clearable></el-input>
          </el-form-item>
          <el-form-item label="部门" size="mini">
            {{(function(){if(params.department_id == 0)params.department_id = null})()}}
            <el-select clearable v-model="params.department_id" placeholder="请选择" filterable @focus="DepartmentListInvok" @change="departmentChange">
              <el-option :label="item.department_name" :value="item.department_id" :key="item.department_id" v-for="item in departmentList"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="回款方式" size="mini">
            <el-select clearable v-model="params.payment_method" @clear="clickClear(params,['payment_method'])" filterable>
              <el-option label="全部" :value="0">全部</el-option>
              <el-option label="客户自结" :value="1">客户自结</el-option>
              <el-option label="物流代收" :value="2">物流代收</el-option>
              <el-option label="电商结算" :value="3">电商结算</el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="物流公司" size="mini">
            <el-select clearable v-model="params.logistics_id" filterable @clear="clickClear(params,['settlement_customer_name'])" @focus="logisticsInvok" @change="getlogisticsChange">
              <el-option :label="item.logistics_name" :value="item.logistics_id" :key="item.logistics_id" v-for="item in logisticsList"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="客户分类" size="mini">
            <el-select clearable v-model="params.customer_type_id" filterable @focus="customerTypeInvok" @change="customerTypeChange">
              <el-option :label="item.customer_type_name" :value="item.customer_type_id" :key="item.customer_type_id" v-for="item in customerTypeList"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="备注" size="mini">
            <el-input v-model="params.abstract" placeholder="请输入" @keyup.enter.native="onSearch(1)" clearable></el-input>
          </el-form-item>
          <el-form-item label="开票信息" size="mini">
            <el-input v-model="params.invoice_info" @keyup.enter.native="clickSearch(1)" clearable=""></el-input>
          </el-form-item>
          <el-form-item size="mini">
            <el-checkbox v-model="params.is_zero" false-label="0" true-label="1" name="type" style="margin:0px 22px 0 62px">仅显示打印次数为0的单据</el-checkbox>
          </el-form-item>
          <el-form-item size="mini">
            <el-checkbox v-model="params.is_freeze" false-label="0" true-label="1" name="type" style="margin:0px 22px 0 62px">仅显示冻结单据</el-checkbox>
          </el-form-item>
          <el-form-item size="mini" class="search-style search">
            <el-button @click="onSearch(1)" size="mini" type="primary">查询</el-button>
            <el-button plain @click="onReset" size="mini">重置</el-button>
          </el-form-item>
        </el-form>
      </div>
      <div class="add_delet">
        <el-button icon="el-icon-circle-plus-outline" type="primary" @click="addF" class="addBtn" size="mini" v-if="authority.b_add==1">新增</el-button>
        <el-button plain icon="el-icon-delete" @click="deletes" size="mini" v-if="authority.b_del==1">删除</el-button>
        <el-button icon="iconfont icon-pass" type="primary" @click="audit" size="mini" v-if="authority.b_pass==1">审核</el-button>
        <el-button plain icon="iconfont icon-fanshenhe" @click="cancellation('salesAuditorNoPass')" size="mini" v-if="authority.b_nopass==1">弃审</el-button>
        <el-button @click="downLoadFile" plain icon="el-icon-download" v-if="authority.b_export_file==1" size="mini">下载模板</el-button>
        <el-button plain icon="iconfont icon-daoru5" v-if="authority.b_import==1" @click="dialogEmployee" class="addBtn" size="mini">导入</el-button>
        <el-dropdown style="margin:0 10px;" v-if="authority.b_export==1">
          <span split-button style="cursor:pointer;color:#4e90e2">
            <el-button plain icon="iconfont icon-daochu10" size="mini">导出</el-button>
          </span>
          <el-dropdown-menu slot="dropdown" style="margin-top:10px;">
            <el-dropdown-item @click.native="exportOrder(1)">导出Excel文件</el-dropdown-item>
            <el-dropdown-item @click.native="exportOrder(2)">导出CSV逗号分隔</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
        <el-dropdown :style="{'margin-left':(authority.b_export==1?0:'10px')}" @command="printChange">
          <span split-button style="cursor:pointer;color:#4e90e2">
            <el-button plain size="mini" icon="el-icon-printer">批量打印</el-button>
          </span>
          <el-dropdown-menu slot="dropdown" style="margin-top:10px;">
            <el-dropdown-item command="1" v-if="authority.b_printingpick == 1">拣货单</el-dropdown-item>
            <el-dropdown-item command="2" v-if="authority.b_printingoutstock == 1">出库单</el-dropdown-item>
            <el-dropdown-item command="3" v-if="authority.b_printingpartslist == 1">汽配单</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
      <xTable :ajax='{ url: "salesOutstockListV2", params: params }' ref='xTable' @getData="dealData" :footerMethod='footerMethod' :tableColumn='tableColumn' :xData='tableData' :isShowTool='true' :dragable="sourceType == 1" />
    </div>
    <div v-if="printingStatusGet">
      <lodopDelivery ref="lodopDelivery" @Search="onSearch" :title='title'></lodopDelivery>
    </div>
    <upload :options="options" @receiveUploadMsg="onSearch" ref="upload"></upload>
  </div>
</template>

<script>
import { repertory } from "common/utils/public_function/commonUtils";
import lodopDelivery from "common/components/public/PublicPrint.vue"; //出库单打印组件
import { TableHead } from "@/components/element/sales/salesoutmanage/salesout/config/salesoutIndex.js";
import { mapActions, mapGetters } from "vuex";
export default {
  mixins: [TableHead],
  data() {
    return {
      options: {
        title: "销售出库",
        url: "/sales/outstockExcel/importClaim",
      },
      title: null,
      auditValue: null,
      loading: false,
      params: {
        warehouse_id: [],
      },
      printstack: true,
      hasAuditorStatusPrint: null,
    };
  },
  props: ["sourceType"],
  components: {
    lodopDelivery,
  },
  computed: {
    ...mapGetters(["printingStatusGet"]),
  },
  methods: {
    ...mapActions(["printingStatus"]),
    // 清除物流公司字段
    clickClear(obj, keyArr) {
      keyArr.forEach((key) => {
        obj[key] = "";
      });
    },
    // 客户分类切换
    customerTypeChange(val) {
      this.customerTypeList.some((item) => {
        if (item.customer_type_id == val) {
          this.params.customer_type_name = item.customer_type_name;
          return true;
        }
      });
    },
    warehouseChange(val) {
      let warehouseList = [];
      if (val != "") {
        val.forEach((item) => {
          let warehouse_name = this.warehouseList.filter(
            (obj) => obj.warehouse_id === item
          )[0].warehouse_name;
          warehouseList.push({
            warehouse_name: warehouse_name,
            warehouse_id: item,
          });
        });
        // localStorage.setItem("warehouseList", JSON.stringify(warehouseList));
      }
    },
    dialogEmployee() {
      this.$refs.upload.stateOpen();
    },
    exportOrder(scheduleType) {
      // 导出库存收发明细表导出URL
      let instaidarry = this.multipleSelection.map((item) => {
        return item.outstock_id;
      });
      let s = Object.assign({}, this.params, {
        export_type: scheduleType,
        id_arr: instaidarry.toString(),
      });
      this.$axMethod.salesOutExportUrl(s).then((res) => {
        if (res.data.code == 200) {
          window.location.href = res.data.data.url;
        }
      });
    },
    downLoadFile() {
      this.$axMethod.salesOutdownloadFile({}).then((response) => {
        if (response.data.code == 200) {
          window.location.href = response.data.data.url;
        }
      });
    },
    handleCommand(common, scope) {
      if (["seeF", "modifyF"].includes(common)) {
        this[common](scope);
      } else {
        this.lodopDe(scope, 0, common);
      }
    },
    printChange(val) {
      let companyFlag = false;
      const companyIds = this.multipleSelection;
      for (var i = 1; i < companyIds.length; i++) {
        if (companyIds[i - 1].company_id != companyIds[i].company_id) {
          companyFlag = true;
          break;
        }
      }
      if (!this.multipleSelection.length) {
        this.$message({
          message: "请选择单据进行打印",
          type: "error",
        });
      } else if (companyFlag) {
        this.$message({
          message: "请选择相同公司单据进行打印",
          type: "warning",
        });
      } else {
        let flag = true,
          errorGoodsArr = [];
        this.multipleSelection.forEach((item) => {
          if (item.after_auditor_print_status == 1) {
            //审核后才可打印
            if (item.auditor_status != 2) {
              errorGoodsArr.push(item.outstock_sn);
              flag = false;
            }
          }
        });
        if (flag) {
          if (val == 1) {
            // 批量打印改造
            this.$set(this, "title", "salesPickingOrders");
            this.printingStatus(true);
            this.$nextTick(() => {
              this.$refs.lodopDelivery.printList(this.multipleSelection, [
                "outstock_id",
              ]);
            });
            // this.multipleSelection.forEach(item => {
            //   this.lodopDe(item, true, "salesPickingOrders");
            // });
            //拣货单
          } else if (val == 2) {
            this.$set(this, "title", "outboundOrder");
            this.printingStatus(true);
            //出库单
            // this.multipleSelection.forEach(item => {
            //   this.lodopDe(item, true, "outboundOrder");
            // });
            this.$nextTick(() => {
              this.$refs.lodopDelivery.printList(this.multipleSelection, [
                "outstock_id",
              ]);
            });
          } else {
            this.$set(this, "title", "autoPartsList");
            this.printingStatus(true);
            this.$nextTick(() => {
              this.$refs.lodopDelivery.printList(this.multipleSelection, [
                "outstock_id",
              ]);
            });
            // //汽配单
            // this.multipleSelection.forEach(item => {
            //   this.lodopDe(item, true, "autoPartsList");
            // });
          }
        } else {
          let str = errorGoodsArr.map((item, i) => `${i + 1}.${item}`);
          this.$alert(
            `以下未审核的单据不可打印：<br/>
            <div style="color:#e6a23c">
              ${str.join("，")}
            <div>`,
            "提示",
            {
              dangerouslyUseHTMLString: true,
            }
          );
        }
      }
    },
    getCustomer(val) {
      if (val != "") {
        this.params.customer_name = this.customerList.filter(
          (obj) => obj.customer_id === val
        )[0].customer_name;
      }
    },
    lodopDe(row, index = 0, name) {
      this.printingStatus(true);
      this.$set(this, "title", name);
      this.$nextTick(() => {
        this.$refs.lodopDelivery.printPdf(
          {
            outstock_id: row.outstock_id,
          },
          index
        );
      });
    },
    employeeChange(val, key) {
      if (val != "") {
        this.params[key] = this.employeeList.filter(
          (obj) => obj.employee_id === val
        )[0].employee_name;
      } else {
        this.params[key] = null;
      }
    },
    companyChange(val) {
      if (val != "") {
        this.params.company_name = this.companyList.filter(
          (obj) => obj.company_id === val
        )[0].company_name;
      }
      this.$set(this.params, "add_user_id", "");
      this.$set(this.params, "auditor_id", "");
      this.$set(this.params, "business_id", "");
      this.$set(this.params, "customer_id", "");
      this.$set(this.params, "department_id", ""); // 初始化部门选中项
      this.$set(this.params, "logistics_id", ""); // 初始化物流公司选中项
      this.$set(this.params, "warehouse_id", ""); // 初始化仓库名称
    },
    getlogisticsChange(val) {
      if (val) {
        this.params.settlement_customer_name = this.logisticsList.filter(
          (obj) => obj.logistics_id === val
        )[0].logistics_name;
      }
    },
    departmentChange(val) {
      if (val) {
        this.params.department_name = this.departmentList.filter(
          (obj) => obj.department_id === val
        )[0].department_name;
      }
    },
    // 新增
    addF() {
      if (this.sourceType === 1) {
        this.$router.push({ path: "/sales/salesout/salesoutAdd" });
      } else {
        this.$router.push({
          path: "/inventory/salesoutstorage/instroageSalesoutAdd",
        });
      }
    },
    // 修改
    modifyF(row) {
      if (row && row.is_execute == 1) {
        this.$message.error("wms系统正在操作此单据，暂不支持单据修改");
        return false;
      }
      if (this.sourceType === 1) {
        this.$router.push({
          path: "/sales/salesout/salesoutModify",
          query: {
            id: row.outstock_id,
            order_id: row.order_id ? row.order_id : "",
          },
        });
      } else {
        this.$router.push({
          path: "/inventory/salesoutstorage/salesoutModify",
          query: {
            id: row.outstock_id,
            order_id: row.order_id ? row.order_id : "",
          },
        });
      }
    },
    // 查看
    seeF(row) {
      if (this.sourceType === 1) {
        this.$router.push({
          path: "/sales/salesout/salesamend",
          query: {
            id: row.outstock_id,
            printStatus: row.after_auditor_print_status,
          },
        });
      } else {
        this.$router.push({
          path: "/inventory/salesoutstorage/salesoutSee",
          query: {
            id: row.outstock_id,
            printStatus: row.after_auditor_print_status,
          },
        });
      }
    },
    audit() {
      let companyFlag = false;
      const companyIds = this.multipleSelection;
      for (var i = 1; i < companyIds.length; i++) {
        if (companyIds[i - 1].company_id != companyIds[i].company_id) {
          companyFlag = true;
          break;
        }
      }
      if (!this.multipleSelection.length) {
        this.$message({
          message: "请选择需要审核的信息",
          type: "warning",
        });
      } else if (companyFlag) {
        this.$message({
          message: "请选择相同公司单据进行审核",
          type: "warning",
        });
      } else {
        var addr = [];
        for (var i = 0; i < this.multipleSelection.length; i++) {
          addr.push({
            order_id: this.multipleSelection[i].outstock_id,
            serial_number: i,
          });
        }
        let obj = {
          company_id: this.multipleSelection[0].company_id,
          bill_id: JSON.stringify(addr),
          operation: 2,
          bill_type: 2,
          is_info: 0,
        };
        this.$axMethod.v2setBillCostControl(obj).then((res) => {
          let s = res.data.data;
          if (res.data.code == 200) {
            if (s.length > 0) {
              if (s[0].bill_setting_status == 0) {
                //提醒逻辑
                let test = "";
                if (this.multipleSelection.length == 1) {
                  s.forEach((item, index) => {
                    test += index + 1 + "." + item.name + "<br>";
                  });
                } else {
                  s = this.$publicFution.deduplication(s, "order_name");
                  s.forEach((item, index) => {
                    test += index + 1 + "." + item.order_name + "<br>";
                  });
                }
                this.$confirm(
                  ` <div class="colorblue">${test}</div> <br>以上商品售价低于成本价，是否继续提交？`,
                  "提示",
                  {
                    confirmButtonText: "确定",
                    cancelButtonText: "取消",
                    dangerouslyUseHTMLString: true,
                    showClose: false,
                    type: "warning",
                    distinguishCancelAndClose: true, // 将取消（点击取消按钮）与关闭（点击关闭按钮或遮罩层、按下 ESC 键）进行区分
                  }
                )
                  .then(() => {
                    this.auditEntity();
                  })
                  .catch((action) => {
                    if (action == "cancel") {
                      // 点击否
                    }
                  });
              } else {
                // 禁止逻辑
                this.$message({
                  message: "商品售价低于成本价，审核失败！",
                  type: "warning",
                });
                s.forEach((item, index) => {
                  this.colorFlag.push(item.order_name);
                });

                this.tableData.forEach((item) => {
                  if (this.colorFlag.indexOf(item.outstock_sn) != -1) {
                    item.canNotDelete = true;
                  }
                });
                this.tableData.push({});
                this.tableData.pop();
              }
            } else {
              this.auditEntity();
            }
          }
        });
      }
    },
    auditEntity() {
      let vm = this;
      var addr = [];
      for (var i = 0; i < this.multipleSelection.length; i++) {
        addr.push(this.multipleSelection[i].outstock_id);
      }
      var rebateArr = addr.toString();
      var funcan = {
        examineID: "outstock_id",
        eachData: {
          outstock_id: rebateArr,
        },
        examineApi: "salesOutstockAuidtorPass",
        list: {
          UrlApi: "salesOutstockListV2",
          data: this.tableData,
          esData: this.params,
        },
      };
      // 校验对接车郎中
      this.$baseData
        .checkCarDoctor({
          bill_type: 9,
          bill_id: rebateArr,
          operation_type: 4,
        })
        .then(() => {
          // 校验安全库存金额
          this.$axMethod
            .safetyStockCheck({
              bill_logogram: "xsck",
              order_type: 2,
              bill_id: rebateArr,
            })
            .then(() => {
              repertory(
                this,
                {
                  goods_info: rebateArr,
                  status: 2,
                  type: 1,
                },
                function () {
                  vm.$common.examineFun(funcan, vm.onSearch);
                }
              );
            });
        });
    },
    cancellation(API) {
      if (this.multipleSelection.length < 1) {
        let txt = "";
        if (API == "salesOutstockAuditorInvalid") {
          txt = "作废";
        } else if (API == "salesAuditorNoPass") {
          txt = "弃审";
        }
        this.$message({
          message: `请选择需要${txt}的信息`,
          type: "warning",
        });
        return false;
      }
      var addr = [];
      for (var i = 0; i < this.multipleSelection.length; i++) {
        addr.push(this.multipleSelection[i].outstock_id);
      }
      var rebateArr = addr.toString();
      var funcan = {
        examineID: "outstock_id",
        eachData: {
          outstock_id: rebateArr,
        },
        examineApi: API,
        list: {
          UrlApi: "salesOutstockListV2",
          data: this.TableData,
          esData: this.searchParams.params,
        },
      };
      // 校验对接车郎中
      this.$baseData
        .checkCarDoctor({
          bill_type: 9,
          bill_id: rebateArr,
          operation_type: 5,
        })
        .then(() => {
          this.$common.examineFun(funcan, this.onSearch);
        });
    },
    onReset() {
      /** 重置时 如需保存默认参数，则： */
      this.$set(this, "params", {
        auditor_status: 0, // wz 审核状态
        return_price_status: 0, // wz 回款状态
        deliver_status: 0, // wz 发货状态
        payment_method: 0, // wz 回款方式
        is_zero: 0,
        is_freeze: 0, //仅显示冻结单据
      });

      this.timeValue = [];
      this.auditValue = [];
      this.onSearch();
    },
    onSearch(number) {
      this.footerData = {};
      if (this.timeValue) {
        this.params.business_start_time = this.timeValue[0];
        this.params.business_end_time = this.timeValue[1];
      } else {
        this.params.business_start_time = this.params.business_end_time = "";
      }
      if (this.auditValue) {
        this.params.auditor_start_time = this.auditValue[0];
        this.params.auditor_end_time = this.auditValue[1];
      } else {
        this.params.auditor_start_time = this.params.auditor_end_time = "";
      }
      this.$refs.xTable._getData(number);
    },
    //删除
    deletes() {
      if (this.multipleSelection.length < 1) {
        this.$message({
          message: `请选择需要删除的信息`,
          type: "warning",
        });
        return false;
      }
      var addr = [];
      for (var i = 0; i < this.multipleSelection.length; i++) {
        addr.push(this.multipleSelection[i].outstock_id);
      }
      var rebateArr = addr.toString();
      var deledata = {
        deleID: "outstock_id",
        deleSelect: this.multipleSelection,
        deleApi: "salesOutstockDelOutstock",
        list: {
          UrlApi: "salesOutstockListV2",
          data: this.TableData,
          esData: this.searchParams.params,
        },
      };
      // 校验对接车郎中
      this.$baseData
        .checkCarDoctor({
          bill_type: 9,
          bill_id: rebateArr,
          operation_type: 3,
        })
        .then(() => {
          this.$common.deletes(deledata, "删除", this, this.onSearch);
        });
    },
  },
  created() {
    this.hasAuditorStatusPrint = window.localStorage.hasAuditorStatusPrint;
    this.authority = JSON.parse(localStorage.getItem("authority"));
    this.$set(this, "params", {
      auditor_status: 0, // wz 审核状态
      return_price_status: 0, // wz 回款状态
      deliver_status: 0, // wz 发货状态
      payment_method: 0, // wz 回款方式
    });
  },
  activated() {
    this.checked = localStorage.getItem("isShowOrderMoney") !== "no";
    this.onSearch(window.goPage);
  },
};
</script>
